body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: rgb(25, 23, 34);
    font-family: 'Righteous', sans-serif;
    
}


.title {
    text-align: center;
    color: yellow;
    font-size: 5rem;
}
.highScore {
    text-align: right;
    color: yellow;

}

.count {
    color: yellow;
    margin-bottom: 0;
    bottom: 0;
}

.imgDiv {
    text-align: center;
    margin-top: 10px;
    padding: 5px;

}

.countDiv {
  margin-top: auto;
}

.scoreDiv {
    margin-top: auto;

}
img {
    width: 100%;
    height: 200px;
    text-align: center;
    object-fit: cover;
    border-radius: 3px;
}

img:hover {
    cursor: pointer;
    opacity: 0.7;
}

